import React, { useEffect, useState } from 'react';
import { supabase } from '../Utilities/supabaseClient';  //    
import { useParams, useNavigate } from 'react-router-dom';  //    URL
import '../App.css'; //    
const ContantLevel = () => {
  const { id } = useParams();  //  `id`  URL
  const [courses, setCourses] = useState([]);  //    
  const [loading, setLoading] = useState(true);  //  
  const navigate = useNavigate();  //  

  //       `id`
  const fetchCourses = async () => {
    const { data, error } = await supabase
      .from('courses')
      .select('*')
      .eq('level_id', id);  //      `level_id`

    if (error) {
      console.error('Error fetching courses:', error);
    } else {
      setCourses(data);  //    
    }
    setLoading(false);  //   
  };

  //        `id`
  useEffect(() => {
    fetchCourses();
  }, [id]);  //     `id`

  return (
    <div className="bg-[#CDC0B6] min-h-screen py-10 px-4">
      <h1 className="text-center text-[#665446] text-4xl font-bold mb-10">   {id}</h1>

      {loading ? (
        <p className="text-center text-[#665446]">  ...</p>
      ) : courses.length === 0 ? (
        <p className="text-center text-[#665446]">    .</p>
      ) : (
        <div className="flex flex-col gap-6 max-w-3xl mx-auto">
          {courses.map((course) => (
            <div
              key={course.id}
              onClick={() => navigate(`/course/${course.id}`)}  //     
              className="cursor-pointer bg-[#FFF9EF] rounded-xl shadow-md p-6 hover:shadow-lg transition"
            >
              <h2 className="text-[#665446] text-2xl font-bold mb-2">{course.name}</h2>
              <p className="text-[#665446] text-base">{course.description}</p>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default ContantLevel;
