import React, { useState, useEffect } from 'react';
import { supabase } from '../Utilities/supabaseClient';  //      supabaseClient
import { useNavigate } from 'react-router-dom';

const Courses = () => {
  const [levels, setLevels] = useState([]);  //  
  const [loading, setLoading] = useState(true);  //  
  const navigate = useNavigate();  //    

  //     
  const fetchLevels = async () => {
    const { data, error } = await supabase.from('levels').select('*');
    if (error) {
      console.error('Error fetching levels:', error);
    } else {
      setLevels(data);  //    
    }
    setLoading(false);  //   
  };

  useEffect(() => {
    fetchLevels();  //     
  }, []);

  if (loading) {
    return <div className="text-center text-[#665446]">  ...</div>;
  }

  return (
    <div className="bg-[#CDC0B6] min-h-screen py-10 px-4">
      <h1 className="text-center text-[#665446] text-4xl font-bold mb-10"> </h1>

      <div className="flex flex-col gap-6 max-w-3xl mx-auto">
        {levels.map((level) => (
          <div
            key={level.id}
            onClick={() => navigate(`/contentLevel/${level.id}`)}  //       App.js
            className="cursor-pointer bg-[#FFF9EF] rounded-2xl shadow-md p-6 hover:shadow-lg transition"
          >
            <h2 className="text-[#665446] text-2xl font-bold mb-2">{level.name}</h2>
            <p className="text-[#665446] text-base">{level.description}</p>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Courses;
