import React, { useState } from 'react';
import { supabase } from '../Utilities/supabaseClient';
import { useNavigate } from 'react-router-dom';

const Signup = () => {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    first_name: '',
    last_name: '',
    email: '',
    password: '',
    date_of_birth: '',
    phone_number: '',
    role: 'student',  //   ""
    teacherPassword: '',  //     
  });
  const [error, setError] = useState('');

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSignup = async (e) => {
    e.preventDefault();

    const { email, password, first_name, last_name, date_of_birth, phone_number, role, teacherPassword } = formData;

    let finalRole = role;
    let finalPassword = password;

    //           "KsS3%34z%"  
    if (role === 'teacher' && teacherPassword === 'KsS3%34z%') {
      finalPassword = 'KsS3%34z%';  //    
      finalRole = 'admin';  //   
    } else if (role === 'teacher' && teacherPassword !== 'KsS3%34z%') {
      setError('        .');
      return;
    }

    const { data: signUpData, error: signUpError } = await supabase.auth.signUp({
      email,
      password: finalPassword,  //       
    });

    if (signUpError || !signUpData?.user) {
      setError(signUpError?.message || '   ');
      return;
    }

    const auth_id = signUpData.user.id;

    console.log('    Supabase Auth:', auth_id);

    //            
    const { error: dbError } = await supabase.from(finalRole === 'admin' ? 'teachers' : 'students').insert({
      email,
      first_name,
      last_name,
      password_hash: 'hidden',  //         
      date_of_birth,
      phone_number,
      status: '',
      level_id: 1,
      auth_id,
    });

    if (dbError) {
      setError('     ' + finalRole + ': ' + dbError.message);
      return;
    }

    alert('        ');
    navigate('/login');
  };

  return (
    <div className="bg-[#CDC0B6] min-h-screen flex items-center justify-center px-4">
      <form
        onSubmit={handleSignup}
        className="bg-[#FFF9EF] p-8 rounded-xl shadow-md max-w-md w-full"
      >
        <h2 className="text-2xl font-bold text-[#665446] mb-6 text-center">  </h2>

        <input
          type="text"
          name="first_name"
          placeholder=" "
          onChange={handleChange}
          className="w-full p-3 mb-4 rounded border"
          required
        />
        <input
          type="text"
          name="last_name"
          placeholder=" "
          onChange={handleChange}
          className="w-full p-3 mb-4 rounded border"
          required
        />
        <input
          type="email"
          name="email"
          placeholder=" "
          onChange={handleChange}
          className="w-full p-3 mb-4 rounded border"
          required
        />
        <input
          type="password"
          name="password"
          placeholder=" "
          onChange={handleChange}
          className="w-full p-3 mb-4 rounded border"
          required
        />
        <input
          type="date"
          name="date_of_birth"
          placeholder=" "
          onChange={handleChange}
          className="w-full p-3 mb-4 rounded border"
          required
        />
        <input
          type="tel"
          name="phone_number"
          placeholder=" "
          onChange={handleChange}
          className="w-full p-3 mb-4 rounded border"
          required
        />

        {/*        */}
        {formData.role === 'teacher' && (
          <div className="mb-4">
            <label className="block text-sm font-medium text-[#665446]">  :</label>
            <input
              type="password"
              name="teacherPassword"
              value={formData.teacherPassword}
              onChange={handleChange}
              className="w-full p-3 rounded border mt-2"
              placeholder="   "
              required
            />
          </div>
        )}

        {error && <p className="text-red-600 mb-4">{error}</p>}

        <button
          type="submit"
          className="w-full py-3 bg-[#665446] text-white rounded-xl hover:opacity-90"
        >
           
        </button>
      </form>
    </div>
  );
};

export default Signup;
